import GoogleSVG from 'SvgComponents/GoogleSVG/GoogleSVG';
import { LinkGoIn } from './ContinueVia.styled';
import FacebookSVG from 'SvgComponents/FacebookSVG/FacebookSVG';

export default function ContinueVia() {
  return (
    <>
      <LinkGoIn>
        <GoogleSVG />
        <p>
           <span> Google</span>
        </p>
      </LinkGoIn>
      <LinkGoIn>
        <FacebookSVG />
        <p>
           <span> Facebook</span>
        </p>
      </LinkGoIn>
    </>
  );
}
